@media only screen {
    * {
        box-sizing: border-box;
    }

    .grid-cardmenu {
        display: grid;
        grid-template-rows: 80% 20%;
        /* grid-gap: 3%; */
        margin: 3%;
    }

    .grid-description {
        display: grid;
        grid-template-columns: 80% 20%;
    }

    .cardmenu__text{
        font-family: 'Quicksand', sans-serif;
        font-size: 15px;
        text-align: center;
    }
    
    .cardmenu__image{    
        cursor: pointer;
        text-align: center;
    }
    
    .cardmenu__image--border{
        border-radius: 8px;
         width: 75%
    }
    
    .cartmenu__cart{
        cursor: pointer;
        /* margin-right: 50%; */
    }
    
    .cartmenu__button__delete{
        cursor: pointer;
        /* margin-right: 50%; */
    }
    
    div.inline{
        float: left;
    }

    .menu-name {
        font-weight: bold;
    }
    
    .cardmenu__undermenu{
        padding: 0 15%;
        margin-top: 7px;
    }

    .on-click {
        width: 25px;
        height: 25px;
        transition: width 200ms, height 200ms;
    }
    
    .on-click:hover {
        width: 32px;
        height: 32px;
    }

    .on-click-img {
        width: 100%;
        height: 100%;
        margin: 0;
        transition: width 200ms, height 200ms, margin 200ms;
    }
    
    .on-click-img:hover {
        width: 108%;
        height: 108%;
        margin: -4% -4% 0 -4%;
    }
}

@media only screen{
    .menuzone{
        display: grid;
        width: 100%;
        justify-content: center;  
        align-content: center; 
        
        grid-template-columns: 12.5% 75% 12.5%;
        /* grid-template-rows: 50% 50%; */
        margin: 3% 0;
    }
    
    .full-height {
        height: 100% !important;
    }

    .mergerow--left{
        cursor: pointer;
        /* grid-area: 1/1/3/2; */
        align-self: center;
    
    }
    
    .mergerow--right{
        cursor: pointer;
        /* grid-area: 1/3/3/4; */
        align-self: center;
    }

    .menuzone--padding{
        padding: 0;
    }

    .menuzone__image--fix{
        margin: 10px 0;
    }

    .cardcartbox--delete{
        cursor: pointer;
    }

    .center{
        margin: 0 auto 0 auto;
    }

    .full {
        width: 100%;
        margin: 0 auto;
    }
}

